@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap);
* {
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
}
body,
p,
a,
ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
li {
  list-style-type: none;
}

.header{text-align:center;font-size:28px;font-weight:300;margin-bottom:30px}.subheader{font-size:20px;font-weight:700;margin-bottom:10px}.text{font-size:13px;line-height:1.5em;margin-bottom:15px}.currency{color:#fd6542;font-weight:700}.flex-search-container .search,.flex-search-container .filter{margin-bottom:10px}@media screen and (min-width: 620px){.flex-search-container{display:flex;margin-bottom:20px}.flex-search-container .search{width:80%}.flex-search-container .filter{width:20%}}@media screen and (min-width: 480px){.header{font-size:38px}.subheader{font-size:22px}.text{font-size:15px}}

body {
  background-color: #f3f7f9;
}

@media screen and (min-width: 620px) {
  body {
    max-width: 600px;
    margin: auto;
  }
}

